

import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import axios from 'axios'
import {
    mapState, mapActions
}
from 'vuex'
import HeaderBar from '../../components/header.vue'
export default {
    components: {
        HeaderBar,
        XInput,
        Group,
        Tabbar,
        TabbarItem,
        Radio,
        Scroller
    },
    methods: {
        ...mapActions(['setRegistered']),
            change(value) {
                console.log('change', value)
            },
            checkValid() {
                const system_name = this.system_name
                const last_name = this.last_name
                const first_name = this.first_name
                const cell_phone = this.cell_phone
                const local_phone = this.local_phone
                const email = this.email
                const password = this.password
                const confirm_password = this.confirm_password
                let check = false

                if (!system_name) {
                    check = true
                } else
                if (!last_name) {
                    check = true
                } else
                if (!first_name) {
                    check = true
                } else
                if (!cell_phone) {
                    check = true
                } else
                if (!email || !this.$refs.email.valid) {
                    check = true
                } else
                if (!password) {
                    check = true
                } else
                if (!confirm_password || !this.$refs.confirm_password.valid) {
                    check = true
                }
                return check
            },
            saveButton() {
                if (this.checkValid()) {
                    this.$vux.toast.show({
                        text: '',
                        type: "cancel"
                    })
                    return
                }

                let options = {
                    SystemName: this.system_name,
                    LastName: this.last_name,
                    FirstName: this.first_name,
                    Sex: this.sex,
                    Cellphone: this.cell_phone,
                    LocalPhone: this.local_phone,
                    email: this.email,
                    ConfirmPassword: this.password,
                    Password: this.confirm_password
                }

                const router = this.$router
                const loading = this.$vux.loading
                const toast = this.$vux.toast
                loading.show({
                    text: 'Loading'
                })

                axios.post('/user', options)
                    .then(function(response) {
                        loading.hide()
                        router.push({
                            'path': '/home'
                        })
                    })
                    .catch(function(error) {
                        loading.hide()
                        toast.show({
                            text: '',
                            type: "cancel"
                        })
                    })

            }
    },
    data() {
        return {
            sex_radio: [{
                key: 'A301',
                value: ''
            }, {
                key: 'A302',
                value: ''
            }],
            system_name: "",
            last_name: "",
            first_name: "",
            sex: "A301",
            cell_phone: "",
            local_phone: "",
            email: "",
            password: "",
            confirm_password: ""
        }
    }
}




